import { animate, style, transition, trigger } from '@angular/animations';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { MenuConfig } from '../../models/menu-config';
import { SfMenuService } from '../../sf-menu.service';

@Component({
  selector: 'sf-menu-shortcuts',
  templateUrl: './menu-shortcuts.component.html',
  styleUrls: ['./menu-shortcuts.component.scss'],
  animations: [
    trigger('appear-fast', [transition(':enter', [style({ opacity: 0 }), animate('80ms 120ms ease-in', style({ opacity: 1 }))])]),
  ],
})
export class MenuShortcutsComponent implements OnInit, OnDestroy {
  menuConfig: MenuConfig;
  private configSubs$: Subscription;

  collapsed = false;
  private collapsedSubs$: Subscription;

  constructor(readonly svc: SfMenuService) {}

  ngOnInit() {
    this.configSubs$ = this.svc.menuConfig$.subscribe((cfg) => {
      this.menuConfig = cfg;
    });

    this.collapsedSubs$ = this.svc.menuCollapse$.subscribe((value) => {
      this.collapsed = value;
    });
  }

  ngOnDestroy() {
    this.configSubs$.unsubscribe();
    this.collapsedSubs$.unsubscribe();
  }
}
