import { Component, OnDestroy, OnInit } from '@angular/core';
import { Align } from '@progress/kendo-angular-popup';
import { Subscription } from 'rxjs';
import { MenuConfig, SsoManageAccountConfig } from '../../models/menu-config';
import { MenuEvent, MenuEventType } from '../../models/menu-events';
import { MenuItem } from '../../models/menu-item';
import { SfMenuService } from '../../sf-menu.service';

interface Tenant {
  id: string;
  active: boolean;
}

@Component({
  selector: 'sf-menu-user',
  templateUrl: './menu-user.component.html',
  styleUrls: ['./menu-user.component.scss']
})
export class MenuUserComponent implements OnInit, OnDestroy {
  menuConfig: MenuConfig;
  private configSubs$: Subscription;

  tenants: Tenant[] = [];

  color: string;
  tenantBg: string;

  collapsed = false;
  private collapsedSubs$: Subscription;

  multipleTenantMenu = false;
  anchorAlign: Align = { horizontal: 'right', vertical: 'top' };
  popupAlign: Align = { horizontal: 'left', vertical: 'top' };
  userActions: any;

  userName = 'Username';

  darkMode = false;

  constructor(readonly svc: SfMenuService) {}

  ngOnInit() {
    this.color = this.svc.colors.gray900;
    this.tenantBg = this.svc.colors.gray200;
    this.configSubs$ = this.svc.menuConfig$.subscribe((cfg) => {
      this.menuConfig = cfg;
      this.userActions = {
        userName: { id: 'username', text: this.menuConfig.userName, iconClass: 'fas fa-user-circle' } as MenuItem,
        tenant: { id: 'tenant', text: this.menuConfig.fleetName, iconClass: 'fas fa-building' } as MenuItem,
        help: { id: 'help', text: this.menuConfig.helpText, iconClass: 'fas fa-question-circle' } as MenuItem,
        logout: { id: 'logout', text: this.menuConfig.logoutText, iconClass: 'fas fa-sign-out-alt' } as MenuItem
      };
      this.tenants.length = 0;
      if (cfg.multipleTenants?.length > 1) {
        for (const t of cfg.multipleTenants.sort((a, b) => a.localeCompare(b))) {
          this.tenants.push({ id: t, active: t === cfg.fleetName });
        }
      }
    });

    this.collapsedSubs$ = this.svc.menuCollapse$.subscribe((value) => {
      this.collapsed = value;
    });

    this.svc.currentTheme.subscribe((theme) => (this.darkMode = theme === 'dark'));
  }

  ngOnDestroy() {
    this.configSubs$.unsubscribe();
    this.collapsedSubs$.unsubscribe();
  }

  get disableUserNameClick(): boolean {
    return this.menuConfig.disableUserNameClick && !this.collapsed;
  }

  changeTenant(tenant: Tenant) {
    this.svc.events$.next({
      type: MenuEventType.ChangeTenantClick,
      value: { text: tenant.id }
    } as MenuEvent);
    this.multipleTenantMenu = false;
  }

  showMultipleTenantMenu() {
    if (this.collapsed) {
      this.svc.menuCollapse$.next(false);
    }
    this.svc.events$.next({
      type: MenuEventType.OpenTenantListClick,
      value: null
    } as MenuEvent);
    this.multipleTenantMenu = true;
  }

  hideMultipleTenantMenu() {
    this.multipleTenantMenu = false;
  }

  userOptionClicked(item: MenuItem) {
    if (!this.disableUserNameClick) {
      if (this.collapsed) {
        this.svc.menuCollapse$.next(false);
        return;
      }
      this.svc.events$.next({
        type: MenuEventType.UserActionClick,
        value: item
      } as MenuEvent);
      if (window.innerWidth <= this.svc.SMALL_RESOLUTION && !this.collapsed) {
        this.svc.menuCollapse$.next(!this.svc.menuCollapse$.value);
      }
    }
  }

  setDarkMode() {
    console.log('set dark mode');
    this.svc.setTheme(this.darkMode ? 'dark' : 'light');
  }

  themeIconClicked() {
    console.log('icon clicked');
    if (this.collapsed) {
      this.svc.menuCollapse$.next(false);
    }
  }

  onSsoManageAccount(event: MouseEvent, config: SsoManageAccountConfig) {
    event.stopPropagation();
    if (!config.disable) {
      window.open(config.url);
    }
  }

  sortMultipleTenant(tenants: string[]): string[] {
    if (tenants) {
      return tenants.sort((a, b) => a.localeCompare(b));
    } else {
      return [];
    }
  }
}
