import { MenuItem } from './menu-item';

export class MenuConfig {
  // products
  products: MenuItem[];
  currentProduct?: MenuItem;

  shortcuts: MenuItem[];
  routes: MenuItem[];

  // branding
  logoClass?: string;
  collapsedLogoClass?: string;
  logoUrl?: string;
  collapsedLogoUrl?: string;
  borderColor?: string;

  // user settings
  userName?: string;
  helpText = 'Help';
  logoutText = 'Logout';

  fleetName?: string;
  fleetTitle?: string;
  multipleTenants?: string[];
  versionText?: string;

  // optional menu
  hideMenu = false;
  tooltips = false;

  themeTitle: string;
  denyChangeTheme = false;
  menuIgnoresTheme = true;
  hideProductName = false;
  routeClickPreventCollapse = false;
  ssoManageAccount?: SsoManageAccountConfig;
  disableUserNameClick = false;
}

export class SsoManageAccountConfig {
  url: string;
  disable = false;
  title?: string;
}
